                    <!-- fas fa-trash  -->
                    <span
                      class="tk-layout-inline-block tk-px-margin-left-px16 tk-font-color-red tk-cursor-pointer text-right"
                      style="width: 40px; font-size: 10px"
                      v-if="!item.disable"
                      @click="deleteSpecialDays(index)"
                      >Delete</span
                    >
                    <!-- fas fa-pen-square -->
                    <span
                      class="tk-layout-inline-block tk-px-margin-left-px16 tk-font-color-main tk-cursor-pointer tk-font-weight-bold text-right"
                      style="width: 40px; font-size: 10px"
                      v-if="item.disable"
                      @click="
                        item.disable = false;
                        isEdit = true;
                      "
                      >Edit</span
                    >
                  </div>
                </div>
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div
      class="container-fluid mt--6"
      style="height: 80vh; position: relative"
      v-else
    >
      <loading size="80"></loading>
    </div>

    <modal :show.sync="showStatement" class="tk-popup-outer">
      <template slot="header">
        <h5 class="modal-title" id="exampleModalLabel">Studio Policies</h5>
      </template>
      <div class="tk-popup-content">
        
      </div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showStatement = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        >
        <base-button
          type="main"
          @click.prevent=""
          class="tk-font-color-white tk-border-none"
          >Edit</base-button
        >
      </template>
    </modal>
  </div>
</template>

<script>
import "flatpickr/dist/flatpickr.css";
import commons from "@/util/commons";
import query from "@/services/query";
import { Select, Option } from "element-ui";
import collections from "../../services/collections";
export default {
  components: {
    BaseSlider: () => import("@/components/BaseSlider"),
    [Select.name]: Select,
    [Option.name]: Option,
    ItemList: () => import("../components/layout/ItemList.vue"),
    Loading: () => import("@/tkViews/components/layout/Loading.vue"),
    flatPicker: () => import("vue-flatpickr-component"),
    WeekBasis: () => import("@/tkViews/components/layout/WeekBasis.vue"),
    buttonWithLoading: () =>
      import("@/tkViews/components/layout/ButtonWithLoading"),
  },
  data() {
    return {

      allowRefund: false,
      allowMakeup: false,
      allowMakeupPeriod: false,
      limitedRefund: false,
      rescheduleLimits: false,
      makeupPeriod: false,
      setMakeupAvailability: false,
      makeupAvailablity:
        "Sun: 2p-7p; Mon: 10a-6p; Tue: 10a-6p; Wed: 10a-6p; Thu: 10a-6p; Fri: 10a-6p; Sat: 2p-7p; ",
      targetedHours:
        "Sun: 4hrs; Mon: 6hrs; Tue: 6hrs; Wed: 6hrs; Thu: 6hrs; Fri: 6hrs; Sat: 4hrs; ",
      selectsType: 0,  

      cancelationMonths: 2,
      makeupMonths: 2,
      makeupPeriodMonths: {
        label: "1 week",
        value: 7,
      },
      hours: [],
      times: [],
      months: [],
      days: [
        {
          label: "1 week",
          value: 7,
        },
        {
          label: "2 weeks",
          value: 14,
        },
        {
          label: "3 weeks",
          value: 21,
        },
        {
          label: "4 weeks",
          value: 28,
        },
        {
          label: "5 weeks",
          value: 35,
        },
        {
          label: "6 weeks",
          value: 42,
        },
        {
          label: "7 weeks",
          value: 49,
        },
        {
          label: "8 weeks",
          value: 56,
        },
        {
          label: "3 months",
          value: 90,
        },
        {
          label: "4 months",
          value: 120,
        },
        {
          label: "5 months",
          value: 150,
        },
        {
          label: "6 months",
          value: 180,
        },
        {
          label: "7 months",
          value: 210,
        },
        {
          label: "8 months",
          value: 240,
        },
        {
          label: "9 months",
          value: 270,
        },
        {
          label: "10 months",
          value: 300,
        },
        {
          label: "11 months",
          value: 330,
        },
        {
          label: "1 year",
          value: 365,
        },
      ],

      makeupHourSliders: [
        {
          day: "Sun",
          hours: [],
        },
        {
          day: "Mon",
          hours: [],
        },
        {
          day: "Tue",
          hours: [],
        },
        {
          day: "Wed",
          hours: [],
        },
        {
          day: "Thu",
          hours: [],
        },
        {
          day: "Fri",
          hours: [],
        },
        {
          day: "Sat",
          hours: [],
        },
      ],
      targetHourSliders: [
        {
          day: "Sun",
          hours: 0,
        },
        {
          day: "Mon",
          hours: 0,
        },
        {
          day: "Tue",
          hours: 0,
        },
        {
          day: "Wed",
          hours: 0,
        },
        {
          day: "Thu",
          hours: 0,
        },
        {
          day: "Fri",
          hours: 0,
        },
        {
          day: "Sat",
          hours: 0,
        },
      ],
      specialHoursSliders: [
        // {
        //   date: "12/4 2020 Fri",
        //   hours: [12, 16],
        // },
      ],

      showCancelationHours: false,
      showCancelationTimes: false,
      showCancelationMonths: false,
      showMakeupHours: false,
      showMakeupTimes: false,
      showMakeupMonths: false,
      showMakeupPeriodMonths: false,

      noticeRequired1: false,
      noticeRequired2: false,
      openLimitDays: false,
      policy: {},
      isLoading: true,
      // format: wnumb({decimals: 0}),
      formatAP: {
        to: (num) => {
          let value = String(num);
          if (num < 12) {
            return value.indexOf(".") > -1
              ? parseInt(value) + ":30a"
              : value + ":00a";
          } else if (num === 12) {
            return value + ":00p";
          } else if (num === 12.5) {
            return parseInt(value) + ":30p";
          } else {
            value = String(num - 12);
            return value.indexOf(".") > -1
              ? parseInt(value) + ":30p"
              : value + ":00p";
          }
        },
        from: Number,
      },
      formatHour: {
        to: (num) => {
          let value = parseInt(num);
          if (value < 2) {
            return value + " hr";
          } else {
            return value + " hrs";
          }
        },
        from: Number,
      },
      specialDate: "",
      isButtonLoadingComplete: false,
      isEdit: false,
      showStatement: false,
    };
  },
  watch: {
    noticeRequired1(newVal) {
      if (!newVal) {
        this.policy.refundNoticeRequired = 0;
      } else {
        this.policy.refundNoticeRequired = 2;
      }
    },
    noticeRequired2(newVal) {
      if (!newVal) {
        this.policy.makeupNoticeRequired = 0;
      } else {
        this.policy.makeupNoticeRequired = 2;
      }
    },
    openLimitDays(newVal) {
      if (!newVal) {
        this.policy.limitDays = -1;
      }else {
        this.policy.limitDays = this.makeupPeriodMonths.value
      }
    },
    specialHoursSliders: {
      handler(newValue) {
        let newVal = newValue;
        outer: for (let j = 0; j < newVal.length; j++) {
          let item = newVal[j];
          for (let i = 0; i < item.hours.length; i++) {
            if (i % 2 === 0) {
              if (item.hours[i] == item.hours[i + 1]) {
                if (item.hours.length > 2) {
                  item.hours.splice(i, 2);
                  break outer;
                }
              }
            }
          }
        }

        this.specialHoursSliders = newVal;
        this.initClickSpecialDay();
      },
      deep: true,
    },
  },
  async created() {
    let self = this;

    for (let i = 1; i < 73; i++) {
      self.hours.push({
        label: i > 1 ? i + " hours" : i + " hour",
        value: i,
      });
    }

    for (let i = 1; i < 31; i++) {
      self.times.push({
        label: i > 1 ? i + " times" : i + " time",
        value: i,
      });
    }

    for (let i = 1; i < 7; i++) {
      self.months.push({
        label: i > 1 ? i + " months" : i + " month",
        value: i,
      });
    }

    self.specialDate = self.$tools.formatYYMMDD(new Date().getTime());

    let policy = await query.listPolicyByTeacherId(commons.userId());
    console.log("policy: ", policy);
    self.policy = policy;
    self.noticeRequired1 = policy.refundNoticeRequired !== 0;
    self.noticeRequired2 = policy.makeupNoticeRequired !== 0;
    self.openLimitDays = policy.limitDays > -1;
    self.days.some((item) => {
      if (item.value == policy.limitDays) {
        self.makeupPeriodMonths = item;
      }
    });

    self.cancelationMonths = parseInt(policy.refundLimitTimesPeriod / 30);

    self.makeupMonths = parseInt(policy.makeupLimitTimesPeriod / 30);

    for (let i = 0; i < policy.weekAvailabilityTimes.length; i++) {
      let availability = policy.weekAvailabilityTimes[i];
      self.makeupHourSliders[availability.dayOfWeek].hours.push(
        availability.timePeriod.from.hour +
          availability.timePeriod.from.minute / 60
      );
      self.makeupHourSliders[availability.dayOfWeek].hours.push(
        availability.timePeriod.to.hour + availability.timePeriod.to.minute / 60
      );
    }

    for (let i = 0; i < policy.specialAvailabilityTimes.length; i++) {
      let availability = policy.specialAvailabilityTimes[i];
      let exist = false;

      for (let j = 0; j < self.specialHoursSliders.length; j++) {
        if (
          self.specialHoursSliders[j].timestamp ==
          availability.specialDay * 1000
        ) {
          exist = true;
          self.specialHoursSliders[j].hours.push(
            availability.timePeriod.from.hour +
              availability.timePeriod.from.minute / 60
          );
          self.specialHoursSliders[j].hours.push(
            availability.timePeriod.to.hour +
              availability.timePeriod.to.minute / 60
          );
          break;
        }
      }

      if (!exist) {
        let hours = [];
        hours.push(
          availability.timePeriod.from.hour +
            availability.timePeriod.from.minute / 60
        );
        hours.push(
          availability.timePeriod.to.hour +
            availability.timePeriod.to.minute / 60
        );
        self.specialHoursSliders.push({
          disable: true,
          timestamp: availability.specialDay * 1000,
          hours: hours,
        });
      }
    }

    if (policy.lessonHours.length > 0) {
      for (let i = 0; i < policy.lessonHours.length; i++) {
        self.targetHourSliders[i].hours = policy.lessonHours[i];
      }
    }

    self.isLoading = false;
  },
  methods: {
    disableSpecialDayAvailability() {
      for (let i = 0; i < this.specialHoursSliders.length; i++) {
        this.specialHoursSliders[i].disable = true;
      }
      this.isEdit = false;
    },
    async savePolicy() {
      let self = this;
      console.log(self.policy);
      self.policy.refundLimitTimesPeriod = self.cancelationMonths * 30;
      self.policy.makeupLimitTimesPeriod = self.makeupMonths * 30;
      console.log(self.makeupPeriodMonths.value)
      self.policy.limitDays = self.makeupPeriodMonths.value;

      let target = [];
      self.targetHourSliders.some((item) => {
        target.push(parseInt(item.hours));
      });
      self.policy.lessonHours = target;

      let specialAvailabilityTimes = [];
      self.specialHoursSliders.some((item) => {
        for (let i = 0; i < item.hours.length; i++) {
          if (i % 2 == 0) {
            specialAvailabilityTimes.push({
              dayOfWeek: new Date(item.timestamp).getDay(),
              specialDay: parseInt(item.timestamp / 1000),
              timePeriod: {
                from: {
                  hour: parseInt(item.hours[i]),
                  minute: String(item.hours[i]).indexOf(".") > -1 ? 30 : 0,
                  second: 0,
                },
                to: {
                  hour: parseInt(item.hours[i + 1]),
                  minute: String(item.hours[i + 1]).indexOf(".") > -1 ? 30 : 0,
                  second: 0,
                },
              },
            });
          }
        }
      });
      self.policy.specialAvailabilityTimes = specialAvailabilityTimes;

      self.policy.setAvailabilityTimes = true;
      self.policy.updateTime = String(parseInt(new Date().getTime() / 1000));

      // sendRequest
      // releaseDescriptionTime
      // description

      await collections.policies
        .doc(self.policy.userId)
        .update(self.policy)
        .then(() => {
          self.isButtonLoadingComplete = true;
          self.$notify({
            message: "Update Success!",
            type: "success",
          });
        })
        .catch((err) => {
          self.$notify({
            message: "Update faild, try it again!",
            type: "error",
          });
        });

      self.isButtonLoadingComplete = false;
    },
    saveWeeklyBasisData(data) {
      this.policy.weekAvailabilityTimes = data;
    },
    updateSpecialDayAvailability(value, item) {
      let data = value;
      data.forEach((i) => {
        i = parseFloat(i).toFixed(1);
      });
      item.hours = data;
    },
    sortAvailabilityHours(hours) {
      let temp = [];
      let temp2 = [];
      for (let i = 0; i < hours.length; i++) {
        if (i % 2 == 0) {
          let arr = [];
          arr.push(hours[i]);
          arr.push(hours[i + 1]);
          temp.push(arr);
        }
      }
      temp = temp.sort((a, b) => {
        return a[0] - b[0];
      });
      temp.some((item) => {
        item.some((i) => {
          temp2.push(i);
        });
      });
      return temp2;
    },
    selectRangeIsAvailable(hours, start, end) {
      let isAvailable = false;
      for (let j = 0; j < hours.length; j++) {
        let index = j;
        if (index % 2 == 0) {
          let s = hours[index],
            e = hours[index + 1];

          if (index === 0) {
            if (end < s) {
              isAvailable = true;
              break;
            }
          }

          if (index + 2 < hours.length - 1) {
            let s2 = hours[index + 2];
            console.log(s2);
            if (start > e && end < s2) {
              isAvailable = true;
              break;
            }
          }

          if (index === hours.length - 2) {
            if (start > e && end <= 22.5) {
              isAvailable = true;
              break;
            }
          }
        }
      }
      return isAvailable;
    },
    initClickSpecialDay() {
      // console.log("init click");
      let self = this;
      let container = document.getElementById("tkPolicySpecialDay");

      if (
        !self.$tools.isNull(container) &&
        !self.$tools.isNull(container.children)
      ) {
        for (let i = 0; i < container.children.length; i++) {
          let index = i;
          let connect =
            container.children[index].children[1].children[0].children[0];
          connect.onclick = null;
          connect.onclick = (e) => {
            if (!e.target.classList.contains("noUi-draggable")) {
              let start =
                parseInt((22.5 - 7.5) * (e.offsetX / e.target.offsetWidth)) +
                7.5;
              let end = start + 1;
              let canPush = false;

              let hours = self.sortAvailabilityHours(
                self.specialHoursSliders[index].hours
              );

              canPush = self.selectRangeIsAvailable(hours, start, end);

              if (canPush) {
                hours.push(start);
                hours.push(end);
              } else {
                end = start;
                start = end - 1;
                canPush = self.selectRangeIsAvailable(hours, start, end);

                if (canPush) {
                  hours.push(start);
                  hours.push(end);
                }
              }

              hours = self.sortAvailabilityHours(hours);

              if (!self.specialHoursSliders[index].disable) {
                self.specialHoursSliders[index].hours = hours;
              }
            }
          };
        }
      }
    },
    formatSliderTooltips(length) {
      let tooltips = [];
      for (let i = 0; i < length; i++) {
        tooltips.push(this.formatAP);
      }
      return tooltips;
    },
    formatSliderConnect(item) {
      // [2,2,3,4,5,6,8]
      let connect = [];
      if (item.hours.length % 2 === 0) {
        for (let i = 0; i <= item.hours.length; i++) {
          if (i % 2 === 0) {
            connect.push(false);
          } else {
            connect.push(true);
          }
        }
      }
      return connect;
    },
    deleteSpecialDays(index) {
      this.specialHoursSliders.splice(index, 1);
    },
    addSpecialDayAvailability() {
      let self = this;
      let timestamp = new Date(self.specialDate).getTime();
      if (
        timestamp < new Date().getTime() &&
        self.$tools.formatYYMMDD(timestamp) !==
          self.$tools.formatYYMMDD(new Date().getTime())
      ) {
        self.$notify({
          message: "The past date is invalid, try another one",
          type: "error",
        });
      } else {
        let exist = false;
        self.specialHoursSliders.some((item) => {
          if (item.timestamp === timestamp) {
            exist = true;
          }
        });

        if (exist) {
          self.$notify({
            message: "You've already set the date, try another one",
            type: "error",
          });
        } else {
          self.specialHoursSliders.push({
            disable: false,
            timestamp: timestamp,
            hours: self.formatHoursInDay(timestamp),
          });
          self.specialHoursSliders = self.specialHoursSliders.sort((a, b) => {
            return a.timestamp - b.timestamp;
          });
          self.isEdit = true;
        }
      }
    },
    formatHoursInDay(timestamp) {
      let weekstr = this.$tools.formatWeekday(timestamp);
      let hours = [0, 0];
      this.makeupHourSliders.some((item) => {
        if (item.day == weekstr) {
          hours = item.hours;
        }
      });
      return hours;
    },
    formatMMDDYYWD(timestamp) {
      return (
        this.$tools.formatMMDDYY(timestamp) +
        " " +
        this.$tools.formatWeekday(timestamp)
      );
    },
    getMonths(day) {
      return parseInt(day / 30);
    },
    getInt(value) {
      return parseInt(value);
    },
    getHoursStr(hour) {
      return hour + " hr(s)";
    },
    initList(type) {
      let self = this;
      switch (type) {
        case 0:
          self.showCancelationTimes = false;
          self.showCancelationMonths = false;
          self.showMakeupHours = false;
          self.showMakeupTimes = false;
          self.showMakeupMonths = false;
          self.showMakeupPeriodMonths = false;
          break;
        case 1:
          self.showCancelationHours = false;
          self.showCancelationMonths = false;
          self.showMakeupHours = false;
          self.showMakeupTimes = false;
          self.showMakeupMonths = false;
          self.showMakeupPeriodMonths = false;
          break;
        case 2:
          self.showCancelationHours = false;
          self.showCancelationTimes = false;
          self.showMakeupHours = false;
          self.showMakeupTimes = false;
          self.showMakeupMonths = false;
          self.showMakeupPeriodMonths = false;
          break;
        case 3:
          self.showCancelationHours = false;
          self.showCancelationTimes = false;
          self.showCancelationMonths = false;
          self.showMakeupTimes = false;
          self.showMakeupMonths = false;
          self.showMakeupPeriodMonths = false;
          break;
        case 4:
          self.showCancelationHours = false;
          self.showCancelationTimes = false;
          self.showCancelationMonths = false;
          self.showMakeupHours = false;
          self.showMakeupMonths = false;
          self.showMakeupPeriodMonths = false;
          break;
        case 5:
          self.showCancelationHours = false;
          self.showCancelationTimes = false;
          self.showCancelationMonths = false;
          self.showMakeupHours = false;
          self.showMakeupTimes = false;
          self.showMakeupPeriodMonths = false;
          break;
        case 6:
          self.showCancelationHours = false;
          self.showCancelationTimes = false;
          self.showCancelationMonths = false;
          self.showMakeupHours = false;
          self.showMakeupTimes = false;
          self.showMakeupMonths = false;
          break;
      }
    },
    select(data) {
      let self = this;

      if (self.showCancelationHours) {
        self.policy.refundNoticeRequired = data.value;
      }

      if (self.showCancelationTimes) {
        self.policy.refundLimitTimesAmount = data.value;
      }

      if (self.showCancelationMonths) {
        self.cancelationMonths = data.value;
      }

      if (self.showMakeupHours) {
        self.policy.makeupNoticeRequired = data.value;
      }

      if (self.showMakeupTimes) {
        self.policy.makeupLimitTimesAmount = data.value;
      }

      if (self.showMakeupMonths) {
        self.makeupMonths = data.value;
      }

      if (self.showMakeupPeriodMonths) {
        self.makeupPeriodMonths = data;
        console.log(self.makeupPeriodMonths)
      }
    },
  },
};
</script>
